<template>
  <div
    @click.self = "closeModal"
    class="modal-wrapper">
    <div class="questions-modal">
      <!-- <div>Выбор узла</div> -->
      <h2
        v-if="action==='addNode'"
      >
        Добавить переход
      </h2>
      <h2
        v-if="action==='moveToNode'"
      >
        Перейти к узлу
      </h2>
        <table class="questions-table"> 
          <th>id</th>
          <th>текст</th>
          <!-- <th>вопрос</th> -->
          <tr
            v-for="question in questionsList"
            :key="question.id"
            @click="selectQuestion(question.id)"
            class="table-row"
          >
            <td class="table-cell">{{question.id}}</td>
            <td class="table-cell">{{question.body.infoText}}</td>
            <!-- <td class="table-cell">{{question.body.questionText}}</td> -->

          </tr>
        </table>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      questionsList: []
    }
  },
  props: {
      serverURL: String,
      testId: Number,
      token: String,
      action: String
  },
  methods: {
    GET_QUESTIONS_DATA(){
      axios({
        method: 'get',
        url: `${this.serverURL}/admin/test/${this.testId}/questions`,
        headers: { Authorization: `Bearer ${this.token}` }
      })
      .then((questionsData) => {
        this.questionsList = questionsData.data.data
      })
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },

    closeModal(){
      this.$emit("closeModal")
    },

    selectQuestion(questionId){      
        
      if (this.action === "addNode") {
        this.$emit("addQuestion", questionId)
        this.closeModal()
      }
      if (this.action === "moveToNode") {
        this.$emit("moveToQuestion", questionId)
        this.closeModal()
      }

    }

  },
  mounted() {
    this.GET_QUESTIONS_DATA()
  },
}
</script>

<style>
    .modal-wrapper{
      width: 100%;
      height: 100%;
      position: fixed;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(0, 0, 0, 0.1)
    }
    .questions-modal{
      width: 1100px;
      height: 800px;
      display: flex;
      flex-direction: column;
      background: white;
      border: 1px solid black;
      padding: 5px;
      overflow: auto;
    }
    .questions-table{
      border: 1px solid black;
    }
    .table-cell{
      border: 1px solid black;
    }
    .table-row{
      cursor: pointer;
    }
</style>