<template>
    <div class="warning-wrapper">
        <div class="warning-window">
            <div>
                {{warningText}}
            </div>
            <div class="warning-footer">
                <button
                    @click="closeWarning"
                >
                    Ok
                </button>
            </div>
        </div>
    </div>
  </template>
  
  <script>
    export default {
        props: {
            warningText: String
        },
        methods: {
            closeWarning(){
                this.$emit("closeWarning")
            }            
        }        
    }
  </script>
  
  <style>
    .warning-wrapper{
        position: fixed;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .warning-window{
        width: 300px;
        height: 200px;
        display: flex;
        flex-direction: column;
        border: 1px solid black;
        background-color: silver;
        padding: 20px;
    }
    .warning-footer{
        display: flex;
        justify-content: center;
        margin-top: 20px;
    }
  </style>