<template>
  <div class="main-wrapper">
    <div class="question-page">
      <h2>Вопрос id: {{ questionId }}</h2>
      <!-- <div>
        <button>
          Перейти к списку тестов
        </button>
      </div> -->
      <div>
        <button
          @click="selectQuestion('moveToNode')"
        >
          Перейти на другой узел
        </button>
      </div>
      
      <div>
        <button
          v-for="parent in parentList"
          :key = parent.id
          @click="moveToId(parent.id)"
        >
          Перейти к родителю {{ parent.id }}
        </button>
      </div>

      <div class="text-question-wrapper">
        Текст:
        <textarea
          v-model.lazy="infoText"
          class="text-info-question"
        >
        </textarea>        
      </div>
      
      <div>
        Количество баллов:
        <input
          v-model.lazy="nodePoints"
          type="number"
          class="input-points"
        >
      </div>

      <div
        v-show="false"
        class="text-question-wrapper"
      >
        Вопрос:
        <textarea
          v-model.lazy="questionText"
          class="text-info-question"
        >          
        </textarea>
      </div>
      <hr style="width: 700px;">
      <div>
        <button
          @click="ADD_QUESTION"
        >
          Добавить новый узел
        </button>
        <button
          @click="selectQuestion('addNode')"
        >
          Выбрать узел
        </button>
        <button
          @click="isShowResumeText=!isShowResumeText"
        >
          Показать текст резюме
        </button>
      </div>

      <div class="branch-list">
        <div
          v-for="(item, index) in answerTransitions" 
          :key="index"
          class="branch-item"
        >
          <div class="column-id">
            id: {{ item.transitionId }}
          </div>
          <div>
            <div>Текст ответа</div>
            <textarea
              v-model="item.answerText"
              class="answer-text"
            ></textarea>
          </div>
          <button
          @click="moveToId(item.transitionId)"
          >Перейти</button>
          <button
            @click="CHECK_AND_DELETE(item.transitionId)"
            
          >Удалить узел</button>
          <button
            @click="DELETE_BRANCH(item.transitionId)"
            
          >Удалить ветку</button>

        </div>
      </div>

      <hr v-if="isShowResumeText" style="width: 700px;">      
      <div v-if="isShowResumeText" class="text-question-wrapper">
        Текст резюме:
        <textarea
          v-model.lazy="resumeText"
          class="text-info-question"
        >
        </textarea>        
      </div>

      <hr style="width: 700px;">

      <button
          @click ="saveData"
          class="button-save"
        >Сохранить</button>
      

    </div>

    <QuestionstModal
      v-if="showQuestionsModal"
      :serverURL = "serverURL"
      :testId = "testId"
      :token = "token"
      :action = "actionQuestionsModal"
      @closeModal="closeModal"
      @addQuestion="addQuestion($event)"
      @moveToQuestion = moveToQuestion($event)
    />
    <WarningModal
      v-if="showWarning"
      :warningText = "warningText"
      @closeWarning = "closeWarning"
    />
  </div>

</template>

<script>
  import axios from 'axios'
  import QuestionstModal from "@/components/QuestionsModal.vue"
  import WarningModal from "@/components/WarningModal.vue"

  export default {
    data() {
      return {
        serverURL: "https://dev1.med-game.ru/api/v1",
        // testId: 11,
        // questionId: 563,
        testId: 25,
        questionId: 813,
        parentList: [],
        parentInfoText: "",
        parentQuestionText: "",
        infoText: "",
        nodePoints: 0,
        questionText: "",
        answerTransitions: [],
        showQuestionsModal: false,
        actionQuestionsModal: "",
        showWarning: false,
        warningText: "",
        isShowResumeText: false,
        resumeText: "",

        token: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJleHAiOjE2ODY5NTAwNDguMTU0MzEyLCJpYXQiOjE2ODU4NzAwNDguMTU0MzEyOCwidXNlcm5hbWUiOiJhZG1pbiIsInJvbGVzIjpbImFkbWluLXBhcnQiLCJtYW5hZ2UtdXNlcnMiLCJtYW5hZ2Utcm9sZXMiXX0.X_7KofdcZBP4QLqnWOnPXY0KCDcmBTzm0R8vMcdGIOI"
      }
    },
    components: {
      QuestionstModal,
      WarningModal
    },
    computed: {
      
    },
    methods: {
      GET_QUESTION_DATA(){
        axios({
          method: 'get',
          url: `${this.serverURL}/admin/question/${this.questionId}`,
          headers: { Authorization: `Bearer ${this.token}` }
        })
        .then((questionData) => {
          this.setData(questionData.data.data)
        })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
      },

      UPDATE_QUESTION_DATA(){
        axios({
          url: `${this.serverURL}/admin/question/${this.questionId}`,
          method: 'put',
          headers: { Authorization: `Bearer ${this.token}` },
          data: {
            "id": this.questionId,
            "question_type_id": 2,
            "is_active": true,
            "test_id": this.testId,
            "seq_no": 1,
            "body": {
              "parentList": this.parentList,
              "infoText": this.infoText,
              "nodePoints": this.nodePoints,
              "questionText": this.questionText,
              "answerTransitions": this.answerTransitions,
              "resumeText": this.resumeText
            }
          }
        })
        .catch(function (error) {
          console.log(error);
      })  
      },

      DELETE_QUESTION(questionId){
        axios({
          url: `${this.serverURL}/admin/question/${questionId}`,
          method: 'delete',
          headers: { Authorization: `Bearer ${this.token}` },          
        })
        .then(() => {
          const deleteIndex = this.answerTransitions.findIndex(answer => answer.transitionId === questionId)
          if (deleteIndex>=0) {
            this.answerTransitions.splice(deleteIndex, 1)
          }
          this.UPDATE_QUESTION_DATA()
        })
        .catch(function (error) {
          console.log(error);
      })  
      },      

      CHECK_AND_DELETE(questionId){
        axios({
          method: 'get',
          url: `${this.serverURL}/admin/question/${questionId}`,
          headers: { Authorization: `Bearer ${this.token}` }
        })
        .then((questionData) => {
          if (questionData.data.data.body.answerTransitions.length) {
            this.warningText = "Нельзя удалять объект. У удаляемого объекта есть зависимые узлы."
            this.showWarning = true
            // console.log("У удаляемого объекта есть зависимые узлы: ", questionData.data.data.body.answerTransitions.length);
            // console.log("Нельзя удалять объект");
            return
          }
          if (questionData.data.data.body.parentList.length > 1) {
            this.warningText = "Нельзя удалять объект. У удаляемого объекта больше одного родителя."
            this.showWarning = true
            // console.log("У удаляемого объекта больше одного родителя: ", questionData.data.data.body.parentList);
            // console.log("Нельзя удалять объект");
            return
          }
          this.DELETE_QUESTION(questionId)
        })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
      },

      ADD_QUESTION(){
        axios({
          url: `${this.serverURL}/admin/questions`,
          method: 'post',
          headers: { Authorization: `Bearer ${this.token}` },
          data: {
            // "id": this.questionId,
            "question_type_id": 2,
            "is_active": true,
            "test_id": this.testId,
            "seq_no": 1,
            "body": {
              "parentList": [{"id": this.questionId}],
              "infoText": "",
              "nodePoints": "",
              "questionText": "",
              "answerTransitions": []
            }
          }
        })
        .then((response) => {
          this.answerTransitions.push(
            {
              "transitionId": response.data.data.id,
              "answerText": ""
            }
          )
          this.UPDATE_QUESTION_DATA()
        })
        .catch(function (error) {
          console.log(error);
        })        
      },

      CHANGE_QUESTION(questionId, qBody){
        axios({
          url: `${this.serverURL}/admin/question/${questionId}`,
          method: 'put',
          headers: { Authorization: `Bearer ${this.token}` },
          data: {
            "id": this.questionId,
            "question_type_id": 2,
            "is_active": true,
            "test_id": this.testId,
            "seq_no": 1,
            
            "body": {
              ...qBody
            //   "parentList": this.parentList,
            //   "infoText": this.infoText,
            //   "questionText": this.questionText,
            //   "answerTransitions": this.answerTransitions
            }
          }
        })
        .catch(function (error) {
          console.log(error);
        }) 
      },

      GET_AND_ADD_PARENT(questionId){
        axios({
          method: 'get',
          url: `${this.serverURL}/admin/question/${questionId}`,
          headers: { Authorization: `Bearer ${this.token}` }
        })
        .then((questionData) => {
          const qBody = questionData.data.data.body
          qBody.parentList.push({"id": this.questionId})
          this.CHANGE_QUESTION(questionId, qBody)
        })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
      },

      DELETE_BRANCH(questionId){
        axios({
          method: 'get',
          url: `${this.serverURL}/admin/question/${questionId}`,
          headers: { Authorization: `Bearer ${this.token}` }
        })
        .then((questionData) => {
          const qBody = questionData.data.data.body
          if (qBody.parentList.length < 2) {
            return false
          }          
          const findParentIndex = qBody.parentList.findIndex(x => x.id === this.questionId)
          if (findParentIndex >= 0) {
            
            qBody.parentList.splice(findParentIndex, 1)
            this.CHANGE_QUESTION(questionId, qBody)
            return true
          }
        })
        .then((deleteBranch) => {        
          if (deleteBranch) {
            const branchListIndex = this.answerTransitions.findIndex(answer => answer.transitionId === questionId)
            if (branchListIndex >= 0) {
              this.answerTransitions.splice(branchListIndex, 1)
              this.UPDATE_QUESTION_DATA()
            }
          }
          else {
            this.warningText = "Нельзя удалить ветку, так как у дочернего элемента только один родитель."
            this.showWarning = true
          }

        })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
      },

      setData(data){
        this.parentList = data.body.parentList,
        this.infoText = data.body.infoText,
        this.nodePoints = data.body.nodePoints,
        this.questionText = data.body.questionText,
        this.answerTransitions = data.body.answerTransitions
        this.resumeText = data.body.resumeText ? data.body.resumeText : ""
      },

      saveData(){
        this.UPDATE_QUESTION_DATA()
      },

      moveToId(id){
        this.questionId = id
        this.GET_QUESTION_DATA()
      },

      selectQuestion(action){
        this.actionQuestionsModal = action
        this.showQuestionsModal = true
      },

      closeModal(){
        this.showQuestionsModal = false
        this.actionQuestionsModal = ""
      },
      addQuestion(questionId){
        this.answerTransitions.push(
            {
              "transitionId": questionId,
              "answerText": ""
            }
          )
        this.GET_AND_ADD_PARENT(questionId)
        this.UPDATE_QUESTION_DATA()
        this.actionQuestionsModal = ""
      },
      moveToQuestion(id){
        this.moveToId(id)
        this.actionQuestionsModal = ""
      },
      closeWarning(){
        this.showWarning = false
        this.warningText = ""
      }

    },
    mounted() {
      this.GET_QUESTION_DATA()
    },
    // watch:{
    //   infoText(newInfoText){
    //     console.log(newInfoText);
    //   }
    // }
  }
</script>

<style>
  .main-wrapper{
    display: flex;
    justify-content: center;
  }
  .question-page{
    display: flex;
    flex-direction: column;
    width: 700px;
    margin-top: 50px;
  }
  .branch-list{
    display: flex;
    flex-direction: column;
  }
  .branch-item{
    display: flex;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .text-question-wrapper{
    display: flex;
    /* margin: 20px; */
    margin-top: 20px;
    margin-bottom: 20px;
  }
  button{
    margin: 5px;
  }
  .text-info-question{
    width: 600px;
    height: 100px;
  }
  .column-id{
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
  .answer-text{
    width: 400px;
    height: 100px;
  }
  .button-save{
    height: 40px;
  }
  .input-points{
    height: 30px;
  }
</style>